<template>
  <ul class="case3d">
   <li v-for="(data,index) in caseNameList" :key="index">
     <iframe :src="`../../../static/three/${data.name}.html`" frameborder="0"></iframe>
     <p v-text="data.name" class="l"></p>
     <p class="r" @click="openNewWindow(data.name)">INFO</p>
   </li>
  </ul>
</template>
<script>
export default {
  name: "case3d",
  data() {
    return {
      caseNameList: []
    };
  },
  created() {
    const context = require
      .context("../../../static/three", true, /.(html)$/)
      .keys();
    if (context.length !== 0) {
      context.forEach(element => {
        let str = element.replace("./", "");
        this.$axios.get(`../../../static/three/${element}`).then(res => {
          console.log(res.data)
          // var imgReg = /<img.*?(?:>|\/>)/gi;
          // var srcReg = /src=[\'\"]?([^\'\"]*)[\'\"]?/i;
          // var imgArr = res.data.match(imgReg); // arr 为包含所有img标签的数组
          // if (imgArr) {
            // let src = imgArr[0].match(srcReg);
            this.caseNameList.push({
              // img: src[1],
              name: str.replace(".html", "")
            });
          // }
        });
      });
    }
    console.log(this.caseNameList)
  },
  methods: {
    openNewWindow(name) {
      window.open("../../../static/three/" + name + '.html', "_blank");
    }
  }
};
</script>
<style lang="less" scoped>
ul{
  width:1130px;
  margin:15px auto;
}
li{
  margin-right: 15px;
  margin-bottom: 15px;
  padding:5px;
  width: 360px;
  display: inline-block;
  box-sizing: border-box;
  border: 1px solid salmon;
  iframe{
    width: 100%;
    height: 240px;
  }
  p{
    line-height: 32px;
    margin: 10px 0;    
    display: inline-block;
      color: salmon;
    &.l{
      margin-left:10px;
    }
    &.r{
      margin-right:10px;
      cursor: pointer;
    }
  }
}
</style>



